* { box-sizing: border-box; margin: 0; padding: 0; }

body { 
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); 
  min-height: 100vh; 
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.app { 
  max-width: 900px; 
  margin: 0 auto; 
  padding: 30px 20px; 
}

h1 { 
  text-align: center; 
  color: white; 
  margin-bottom: 30px; 
  font-size: 2.5em; 
  text-shadow: 0 2px 10px rgba(0,0,0,0.3);
}

.form { 
  display: grid; 
  gap: 15px; 
  background: white; 
  padding: 30px; 
  border-radius: 20px; 
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  margin-bottom: 30px;
}

input, select, textarea { 
  padding: 15px; 
  border: 2px solid #e1e5e9; 
  border-radius: 12px; 
  font-size: 16px; 
  transition: all 0.3s ease;
}

input:focus, select:focus, textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

button { 
  padding: 15px 25px; 
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); 
  color: white; 
  border: none; 
  border-radius: 12px; 
  font-size: 16px; 
  font-weight: 600;
  cursor: pointer; 
  transition: all 0.3s ease;
}

button:hover { 
  transform: translateY(-2px); 
  box-shadow: 0 10px 25px rgba(102, 126, 234, 0.4);
}

.stats {
  background: rgba(255,255,255,0.2);
  padding: 15px 25px;
  border-radius: 12px;
  color: white;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  backdrop-filter: blur(10px);
}

.jobs { 
  display: grid; 
  gap: 20px; 
}

.job-card { 
  background: white; 
  padding: 25px; 
  border-radius: 20px; 
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.job-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.15);
}

.job-card::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 6px;
}

.status-applied::before { background: #ffc107; }
.status-interview::before { background: #17a2b8; }
.status-offer::before { background: #28a745; }
.status-rejected::before { background: #dc3545; }
.status-accepted::before { background: #6f42c1; }

.job-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.job-header h3 {
  color: #333;
  margin: 0;
}

.actions {
  display: flex;
  gap: 10px;
}

.actions button {
  padding: 8px 12px;
  background: none;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 16px;
  transition: all 0.3s;
}

.actions button:hover {
  background: #667eea;
  color: white;
  border-color: #667eea;
  transform: scale(1.1);
}

.deadline.overdue {
  color: #dc3545 !important;
  font-weight: bold;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

.status-badge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  color: white;
}

.empty {
  text-align: center;
  padding: 60px 20px;
  color: white;
  font-size: 1.2em;
  background: rgba(255,255,255,0.1);
  border-radius: 20px;
  backdrop-filter: blur(10px);
}

@media (max-width: 768px) {
  .app { padding: 20px 15px; }
  .form { grid-template-columns: 1fr; padding: 20px; }
  .job-header { flex-direction: column; gap: 10px; }
}
